@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600&family=Rubik+Mono+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Nunito', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.nav_bar{display:flex;justify-content:space-between;align-items:center;background-image:linear-gradient(45deg, #ff3d3d, #4848ff);box-shadow:0 0 4px black}.nav_bar img{width:60px;height:60px;margin-left:8px}.nav_bar ul{display:flex;list-style-type:none}.nav_bar ul li{background-color:#5471aa;margin:11px;padding:5px 12px;border-radius:12px}.nav_bar ul li a{text-decoration:none;color:#ffffff}

div .players_card_container{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:20px;gap:20px}.player_card{background-color:rgba(226,226,226,0.4);border-radius:15px;display:flex;flex-direction:column;text-align:center;padding:20px 12px;align-items:center;transition:all 1s;text-decoration:none;color:black}.player_card img{width:80%}.player_card:hover{-webkit-transform:scale(110%);transform:scale(110%)}

.container_portada{width:85%;height:50vh;margin:20px auto;position:relative;z-index:1;display:flex;flex-direction:column-reverse}.container_portada::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;opacity:.9;z-index:-1;background-image:url(/peru-pro-players/static/media/portada.6e146d44.jpg);background-repeat:no-repeat}.title_portada{width:1px;margin-left:18px;font-size:35px;color:#fff;font-family:'Rubik Mono One', sans-serif;padding:0 12px;border-left:8px solid white}@media only screen and (max-width: 600px){.container_portada{width:100%;height:70vh}}

